
import VueI18n from 'vue-i18n';
const langs = {
  'zh_CN': {
    ...($i18n['zh_CN'] || {})
  },
  'en_US': {
    ...($i18n['en_US'] || {})
  }
};
Vue.use(VueI18n);
const i18n = new VueI18n({
  locale: 'en-US',    // , locale,this.$i18n.locale 
  messages: {
    ...langs
  },
  silentTranslationWarn: true
});

export default i18n;